import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    lang: null, // 
    screenWidth: document.body.clientWidth
  },
  mutations: {
    // 
    setLang(state, value) {
      state.lang = value;
    },
    setScreenWidth(state, value) {
      state.screenWidth = value;
    }
  },
  actions: {},
  modules: {}
});
